@import "./variables.scss";

.catalog {
  display: flex;
  flex-wrap: wrap;

  max-width: 944px;
  margin: 0 auto;

  &__card {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 200px;
    height: 300px;
    border: 1px solid $border-color;
    border-radius: 5px;
    margin-bottom: 48px;

    transform-origin: center;
    transition-duration: 300ms;
    transition-timing-function: ease;

    &:hover {
      transform: scale(120%);
      color: $card-color;
    }
  }

  &__card:not(:nth-child(4n)) {
    margin-right: 48px;
  }
}
